/* Text Colors */
.f-default  { color: #383938 !important; }
.f-primary  { color: #FF4B89 !important; }
.f-success  { color: #8BF3FB !important; }
.f-danger   { color: #FE0022 !important; }
.f-info     { color: #FFFD7B !important; }

.f-green  { color: #5FBE71 !important; }
.f-red  { color: #FE0022 !important; }

/* LINKS */
a { color: #FF4B89 !important; }
a:active { text-decoration: none !important; }
a:hover { text-decoration: none; }

p{
   word-break: keep-all;
}



