/* Form Inputs */

.form-group .form-control {
    position: relative;
    transform: scale(.95);
    transition: ease .3s;
    border-radius: 30px;
    padding: 10px 20px;
}

/* Remove ios shadow */
* {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.form-group .form-control:focus {
    transform: scale(1);
    transition: ease .3s;
}

.form-group .form-control::-webkit-input-placeholder { color: #393838; }

.has-error .form-control,
.has-error .form-control:focus,
.has-error .multiselect {
    border: 2px solid #FE0022 !important;
}

.has-error .ql-toolbar.ql-snow,
.has-error .ql-container.ql-snow {
    border: 2px solid #FE0022 !important;
}



